    width: 100%;
    height: 30px;
    padding-top: 7px;
    font-size: 0.8em;
    font-weight: 600;
    text-align: center;
    text-transform: uppercase;
    color: #686A6B;
    border: 1px solid #cacaca;
    background-color: #DDDDDD;
}
.playlist .items_container {
    height: calc(100%-30px);
}
.playlist .item {
    position: relative;
}
.playlist .item::after {
    content: "";
    display: block;
    clear: both;
}
.playlist .item .audio_btn {
    position: absolute;
    left: 0px;
    top: 0px;
}
.playlist .item .audio_nowplaying {
    display: block;
    background: rgba(0,0,0,0.85);
    background-image: url(../../assets/images/nowplaying.gif);
    background-size: 60px;
    background-repeat: no-repeat;
    background-position: center;
    width: 30.5%;
    height: 100%;
}
.playlist .item .audio_pause {
    width: 30.5%;
    height: 100%;
    display: block;
}
.playlist .item .audio_pause::before {
    content: "";
    display: block;
    border: 1px solid white;
    background-color: rgba(0,0,0,0.3);
    border-radius: 50%;
    width: 22px;
    height: 22px;
    position: absolute;
    left: 50%;;
    top: 0%;
    transform: translate(-11px,22px);
}
.playlist .item .audio_pause::after {
    content: "";
    display: block;
    border: 15px solid transparent;
    border-left-color: white;
    position: absolute;
    left: 50%;;
    top: 0%;
    transform: translate(-3px,29px);
    height: 0;
    border-width: 4px 0 4px 8px;
}
.playlist .item img {
    display: block;
    float: left;
    width: 30.5%;
}
.playlist .item .item_info {
    float: left;
    width: 69.5%;
    margin: 12px 0;
    text-align: left;
    padding-left: 15px;
}
.playlist .item .item_info h3 {
    font-size: 0.8em;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    display: -webkit-box;
}
.playlist .item .item_info .nowplaying {
    color: #fff;
    text-transform: uppercase;
    background-color: #3B98F0;
    border-radius: 3px;
    font-size: 0.75em;
    text-align: center;
    width: 110px;
    margin-top: 3px;
    padding: 2px 0;
}
.playlist .item .item_info h4 {
    font-size: 0.75em;
    line-height: 1.4em;
    margin-top: 3px;
    display: block;
    color: #8D8E8F;
}
@media screen and (max-width:1024px){
    .video_watch_container {
        width: 100%;
    }
    .playlist {
        width: 100%;
        z-index: 10001;
    }
    .playlist .item .audio_pause {
        height: 100%;
    }
    .playlist .item .audio_pause::before {
        transform: translate(-11px,-12%);
        top: 50%;
    }
    .playlist .item .audio_pause::after {
        transform: translate(-3px,50%);
        top: 50%;
    }
}

</style>


